import React from 'react'
import { Button ,Flex,Stack} from "@chakra-ui/react"

export default function SideNavbar({setlog,usr,clrusr,setTog}) {

        return( usr?
        <div style={{zIndex:'8',color:'#404040',boxShadow:'1px 1px 10px black',backgroundColor:"white",animation:"ease-in 1s",animationName:"moc",animationDirection:"normal",animationDuration:'0.2s',width:'200px',display:"flex",flexDirection:"column",right:"0",top:"0",bottom:'0',position:"fixed",alignItems:'center'}}
        ><Button onClick={()=>clrusr()} style={{marginTop:"10px",marginBottom:"10px"}} colorScheme="teal" width="120px" variant="outline">
                    Logout
                </Button></div>:<div style={{zIndex:'8',color:'#404040',boxShadow:'1px 1px 10px black',backgroundColor:"white",animation:"ease-in 1s",animationName:"moc",animationDirection:"normal",animationDuration:'0.2s',width:'200px',display:"flex",flexDirection:"column",right:"0",top:"0",bottom:'0',position:"fixed",alignItems:'center'}}>
                <Button onClick={()=>{
                    setTog("none")
                    setlog(true)}} colorScheme="teal" width="120px" style={{marginTop:"10px",marginBottom:"10px"}} variant="outline">
                    Login
                </Button>
                <Button onClick={()=>{
                    setTog("none")
                    setlog(false)}} colorScheme="teal" width="120px" variant="solid">
                    Register
                </Button>
            </div>)
    
}
